  height: 64px;
  width: 765px;
  border-radius: 2px 2px 30px 30px;
}
.header-class ul {
  list-style: none;
  margin: 0;
}
.header-li {
  font-family: "Roboto", sans-serif;
  margin-right: 3rem;
  padding: 1.5rem;
  float: right;
